.consolestation-terminal{
    width: 90%;
    height: 90%;
    margin: auto;
}

.win-control-btn.mine{
    border: none;
    max-width: 30px;
}

.win-control-btn.mine:disabled{
    background-color: transparent;
    color: white;
    opacity: 1;
}

.win-bar{    
    margin-top: 10px;
    margin-left: 1%;
    margin-right: 1%;
    background-color: #2d5cdda3; 
    color: white; 
    padding-left: 10px;
    border-radius: 5px;
    border-bottom: solid 15px;
    border-right: solid;
    padding-bottom: 15px;
}

.content-console{
    width: 100%;
    height: 100%;
}

.win-control-buttons{
    position: fixed;
    right: 0;
    margin-right: 3vh;
}

.wrapper{
    background-color: black;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    position: absolute;
    max-height: 100%;
    max-width: 100%;
    overflow: hidden;
}

.list-format{
    width: 100%;
    max-width: 360px;
    background-color: transparent;
    
}

.list-format .MuiListItem-root{
    background-color: rgba(49, 49, 49, 0.822);
}

.list-format .title{
    background-color: rgba(0, 0, 0, 0.822);
}